@import url("https://fonts.googleapis.com/css2?family=Grape+Nuts&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Caveat&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Cormorant:ital,wght@1,300&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Source+Code+Pro:wght@300&display=swap");
@import url('https://fonts.googleapis.com/css2?family=Overpass:wght@200&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Rubik+Puddles&display=swap');
@media (min-width: 320px) and (max-width: 480px) {
  body {
    background-color: #a59ef0;
  }
  .giff {
    width: 100px;
    height: 100px;
  }
  .rt {
    border: thick double #32a1ce;
    width: auto;
  }
  .content {
    border: 2px solid darkblue;
    border-radius: 7px;
    min-height: 50px;
    max-width: 100%;
    height: auto;
    position: absolute;
    z-index: 12;
    margin-top: 10px;
    margin-right: 8px;
  }
  .shine-button {
    text-decoration: none;
    display: inline-block;
    padding: 10px 30px;
    margin: 10px 20px;
    position: relative;
    overflow: hidden;
    border: 2px solid #fe6637;
    background-color: #f1daa5;
    border-radius: 8px;
    font-family: "Source Code Pro", monospace;
    color: black;
    transition: 0.2s ease-in-out;
  }
  .shine-button:before {
    content: "";
    background: linear-gradient(
      90deg,
      rgba(255, 255, 255, 0.1),
      rgba(255, 255, 255, 0.5)
    );
    height: 50px;
    width: 50px;
    position: absolute;
    top: -8px;
    left: -75px;
    transform: skewX(-45deg);
  }
  .shine-button:hover {
    background: #fe6637;
    color: #fff;
  }
  .shine-button:hover:before {
    left: 150px;
    transition: 0.5s ease-in-out;
  }
  .modal__close {
    width: 20px;
    background-color: red;
    color: white;
    cursor: pointer;
  }
  .modal.open {
    top: 0;
    left: 0;
  }
  .modal.close {
    height: 0;
    display: none;
  }
  .flex-header {
    background-color: #f08eed;
    display: flex;
    justify-content: space-between;
    font-family: "Grape Nuts", cursive;
  }
  .content-text {
    background-color: #f0b2ed;
    font-family: "Grape Nuts", cursive;
  }
  .header-text {
    margin-left: 10px;
    font-weight: bold;
  }
  .image-icon {
    height: 15px;
    width: 15px;
    margin-left: 10px;
    vertical-align: bottom;
  }
  .hello-text {
    font-family: "Caveat", cursive;
    font-size: 23px;
    background-color: #c817e8;
  }
  .hello-header-text {
    font-size: 24px;
    font-family: "Caveat", cursive;
    font-weight: bold;
    background-color: #ae17e8;
  }
  .hello-buton {
    text-decoration: none;
    display: inline-block;
    padding: 10px 30px;
    margin: 10px 20px;
    overflow: hidden;
    border: 2px solid red;
    border-radius: 8px;
    font-family: "Caveat", cursive;
    background-color: red;
    color: white;
    font-weight: bold;
    font-size: 20px;
  }
  .content-text-button {
    background-color: #c817e8;
    display: flex;
    justify-content: center;
  }
  .box {
    font-family: "Cormorant", serif;
    font-weight: bold;
  }
  .header-h4 {
    text-align: center;
    font-family: "Cormorant", serif;
    font-size: 18px;
    font-weight: bolder;
    margin-bottom: 15px;
  }
  .text-moments {
    background-color: #0b1c70;
    color: palevioletred;
    margin-bottom: 0px;
    margin-top: 0px;
  }
  .header-moments {
    background-color: #0b0263;
    color: palevioletred;
    padding-bottom: 6px;
    padding-top: 6px;
  }
  .flex-button-moments {
    display: flex;
    justify-content: center;
    justify-content: space-evenly;
    background-color: #0b1c70;
  }
  .button-moments {
    text-decoration: none;
    display: inline-block;
    padding: 8px 30px;
    margin: 10px 20px;
    overflow: hidden;
    border: 2px solid red;
    border-radius: 8px;
    font-family: "Caveat", cursive;
    background-color: #7d0263;
    color: white;
    font-weight: bold;
    font-size: 20px;
  }
  .a-trip{
    text-decoration: none;
    font-family: "Source Code Pro", monospace;
    color: black;
  }
  .image-gif{
    width: 150px;
    height: 150px;
  }
  .gif-flex{
    display: flex;
    justify-content: space-between;
    margin-top: 30px;
  }
  .header-trip{
    text-align: center;
    font-family: "Cormorant", serif;
    font-size: 18px;
    font-weight: bolder;
    margin-bottom: 15px;
  }
  .header-trip-star{
    text-align: center;
    font-family: "Cormorant", serif;
    font-size: 18px;
    font-weight: bolder;
    margin-top: 15px;
  }
  .bloc-trip{
    font-family: 'Overpass', sans-serif;
  }
  .image-modal24{
    width: 150px;
    height: 200px;
  }
  .header-modal24{
    justify-content: flex-end;
    background-color: wheat;
  }
  .content-modal24{
    text-align: center;
  }
  .text-modal24{
    padding-left: 20px;
    padding-right: 20px;
    font-family: 'Rubik Puddles', cursive;
    padding-bottom: 15px;
  }
  .content-text-modal24{
    background-color: wheat;
  }
  .button-modal24{
    background-color: wheat;
    padding-bottom: 8px
  }
  .button-text-modal24{
    text-decoration: none;
    display: inline-block;
    padding: 8px 20px;
    margin: 10px 20px;
    overflow: hidden;
    border: 2px solid red;
    border-radius: 8px;
    font-family: 'Rubik Puddles', cursive;
    background-color: #f07a4f4f;
    color: black;
    font-weight: bold;
    font-size: 20px;
  }
}
